<template>
  <div class="sign">
    <p class="title">报名信息</p>
    <input v-model="nickName" class="nick-name" type="text" placeholder="昵称" />
    <input v-model="school" class="school" type="text" placeholder="院校名称" />
    <input v-model="age" class="age" type="text" placeholder="年龄" />
    <select class="select-box" v-model="sex">
      <option disabled value="">性别:</option>
      <option value="男">男</option>
      <option value="女">女</option>
    </select>
    <input v-model="height" type="text" placeholder="身高(厘米)" />
    <input v-model="place" type="text" placeholder="家乡" />
    <input v-model="mail" type="text" placeholder="邮箱" />
    <input v-model="qq" type="text" placeholder="联系方式(微信号或手机号)" />
    <select v-model="constellation" name="constellation" class="select-box">
      <option disabled value="">星座:</option>
      <option value="白羊座">白羊座</option>
      <option value="金牛座">金牛座</option>
      <option value="双子座">双子座</option>
      <option value="巨蟹座">巨蟹座</option>
      <option value="狮子座">狮子座</option>
      <option value="处女座">处女座</option>
      <option value="天秤座">天秤座</option>
      <option value="天蝎座">天蝎座</option>
      <option value="射手座">射手座</option>
      <option value="摩羯座">摩羯座</option>
      <option value="水瓶座">水瓶座</option>
      <option value="双鱼座">双鱼座</option>
    </select>
    <textarea
      v-model="about"
      cols="30"
      rows="3"
      placeholder="关于我(15字以上)"
    ></textarea>
    <textarea
      v-model="target"
      cols="30"
      rows="3"
      placeholder="我喜欢的TA(15字以上)"
    ></textarea>
    <select v-model="type" class="select-box">
      <option disabled value="">活动类型:</option>
      <option value="">报名上墙</option>
      <option value="">参加互选</option>
    </select>
    <p>上传照片:</p>
    <p>(先提交信息才能上传照片)</p>
    <p v-show="showUp">照片上传完毕请确认报名</p>
    <AntdUp v-show="showUp" class="ant" />

    <button class="submit" v-on:click="submit">提交信息</button>
    <button class="submit" v-show="showUp" v-on:click="submit2">完成报名</button>
    <div @touchmove.stop.prevent v-show="alert" class="alert">
      <p class="title">提示</p>
      <p class="content">{{ alertContent }}</p>
      <button class="ok" v-on:click="close">好的</button>
    </div>
    <div @touchmove.stop.prevent v-show="alert" class="layer"></div>
  </div>
</template>

<script>
import AntdUp from "../components/Antd-up";

export default {
  mounted() {
    this.openid = localStorage.getItem("openid");
    console.log(this.openid);
  },
  name: "Sign",
  components: {
    AntdUp,
  },
  methods: {
    update: function (e) {
      var self = this;
      let file = e.target.files[0];
      /* eslint-disable no-undef */
      let param = new FormData(); // 创建form对象
      param.append("file", file); // 通过append向form对象添加数据
      param.append("openid", this.openid); // 添加form表单中其他数据
      console.log(param.get("file")); // FormData私有类对象，访问不到，可以通过get判断值是否传进去
      console.log(param.get("openid"));
      let config = {
        headers: { "Content-Type": "multipart/form-data" },
      };
      // 添加请求头
      this.axios.post("url", param, config).then((response) => {
        if (response.data.code === 0) {
          self.ImgUrl = response.data.data;
        }
        console.log(response.data);
      });
    },
    preview: function () {
      console.log("preview");
    },
    exceed: function () {
      console.log("exceed");
    },
    oversize: function () {
      console.log("oversize");
    },
    submit2: function () {},
    submit: function () {
      if (
        this.nickName == "" ||
        this.school == "" ||
        this.age == "" ||
        this.height == "" ||
        this.place == "" ||
        this.about == "" ||
        this.target == "" ||
        this.mail == "" ||
        this.qq == ""
      ) {
        this.alertContent = "您还有信息没有填完!";
        this.alert = true;
        return;
      }
      if (this.nickName.length > 10) {
        this.alertContent = "昵称太长了!";
        this.alert = true;
        return;
      }
      if (this.school.length > 15) {
        this.alertContent = "学校名字太长了!";
        this.alert = true;
        return;
      }
      if (isNaN(Number(this.age))) {
        this.alertContent = "输入的年龄不是数字!";
        this.alert = true;
        return;
      }
      if (isNaN(Number(this.height))) {
        this.alertContent = "输入的身高不是数字!";
        this.alert = true;
        return;
      }
      if (this.place.length > 10) {
        this.alertContent = "家乡请简略一些";
        this.alert = true;
        return;
      }
      if (this.about.length < 15) {
        this.alertContent = "关于您请说详细一些";
        this.alert = true;
        return;
      }
      if (this.target.length < 15) {
        this.alertContent = "请再详细一些描述您喜欢的TA";
        this.alert = true;
        return;
      }
      if (this.about.length > 100) {
        this.alertContent = "关于您的内容请简略一些噢";
        this.alert = true;
        return;
      }
      if (this.target.length > 100) {
        this.alertContent = "关于您喜欢的TA，请简略一些噢";
        this.alert = true;
        return;
      }
      if (this.mail.length > 50) {
        this.alertContent = "没有这么长的邮箱吧";
        this.alert = true;
        return;
      }
      if (this.qq.length > 20) {
        this.alertContent = "没有这么长的qq号吧";
        this.alert = true;
        return;
      }
      if (this.mail.indexOf("@") == -1) {
        this.alertContent = "邮箱格式不正确";
        this.alert = true;
        return;
      }
      this.axios
        .post("http://www.scgxtd.cn/public/user/upload/", {
          nickName: this.nickName,
          openid: localStorage.getItem("openid"),
          sex: this.sex,
          school: this.school,
          age: this.age,
          height: this.height,
          constellation: this.constellation,
          place: this.place,
          mail: this.mail,
          connect: this.qq,
          about: this.about,
          target: this.target,
        })
        .then((res) => {
          if (res.code == 0) {
            this.showUp = true;
          } else if (res.code == -1) {
            alert("报名失败");
          }
        });
      // console.log(this.nickName.length);
      // console.log(Number(this.$data.age));
    },
    close: function () {
      this.$data.alert = false;
    },
  },
  data: function () {
    return {
      type: "",
      files: [], //json数组, {"name":"xxxx", "url":"xxxx"}base64格式
      nickName: "",
      sex: "",
      school: "",
      age: "",
      height: "",
      constellation: "",
      place: "",
      mail: "",
      qq: "",
      about: "",
      target: "",
      openid: "testid",
      alertContent: "",
      alert: false,
      showUp: false,
    };
  },
};
</script>
<style scoped>
.ant {
  position: relative;
  width: 200px;
  margin: 0 auto;
}
.sign {
  position: relative;
  padding-bottom: 55px;
}
input {
  margin: 10px;
  height: 30px;
  width: 180px;
  border-radius: 5px;
  border-style: solid;
  border-color: pink;
}
select {
  display: block;
  margin: 10px auto;
  height: 30px;
  border-style: solid;
  border-radius: 5px;
  border-color: pink;
}
textarea {
  border-style: solid;
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 5px;
  border-color: pink;
  margin: 10px;
}
.submit {
  position: relative;
  margin: 20px;
  border-style: solid;
  border-radius: 5px;
  border-color: pink;
  background-color: pink;
  color: white;
  font-size: 16px;
  height: 40px;
}
.title {
  font-size: 20px;
  margin: 0;
  margin-top: 10px;
}
.layer {
  z-index: 150;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.6;
}
.alert {
  z-index: 200;
  position: fixed;
  border-style: solid;
  border-width: 2px;
  border-radius: 10px;
  border-color: pink;
  width: 95%;
  top: 250px;
  height: 150px;
  background-color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}
.ok {
  border-style: solid;
  border-radius: 5px;
  border-color: pink;
  background-color: pink;
  color: white;
  font-size: 16px;
  height: 40px;
  width: 200px;
}
</style>
